import React from "react";
import styled from "styled-components";
import BlogContentCard from "./ContentCard";
import coverImg from "statics/images/introBgCenter.png";

const contentData = [
  {
    coverImg: coverImg,
    title: "A",
    desc: "B",
    uploadDate: "2020. 1. 1."
  },
  {
    coverImg: coverImg,
    title: "A",
    desc: "B",
    uploadDate: "2020. 1. 1."
  },
  {
    coverImg: coverImg,
    title: "A",
    desc: "B",
    uploadDate: "2020. 1. 1."
  },
  {
    coverImg: coverImg,
    title: "A",
    desc: "B",
    uploadDate: "2020. 1. 1."
  }
];

export default function BlogContentList() {
  return (
    <>
      <ContentListWrap>
        {contentData.map(content => (
          <BlogContentCard key={content.title} content={content} />
        ))}
      </ContentListWrap>
    </>
  );
}

const ContentListWrap = styled.div`
  display: grid;
  grid-template-columns: 18rem 18rem 15rem;
  grid-template-rows: 22rem;
`;
