import React from "react";
import styled from "styled-components";

export default function SearchBar() {
  return (
    <SearchWrap>
      <SearchForm>
        <input type="search" placeholder="Search here..." />
        <i className="fas fa-search"></i>
      </SearchForm>
    </SearchWrap>
  );
}

const SearchWrap = styled.div`
  position: absolute;
  right: 0;
`;

const SearchForm = styled.form`
  width: 50px;
  height: 50px;
  margin: 10px 0px;
  overflow: hidden;
  position: relative;
  background-color: white;
  border-radius: 50px;
  border: 1px solid black;
  transition: all 0.3s;

  & > input {
    height: 50px;
    width: 250px;
    right: -250px;
    position: absolute;
    background: white;
    padding: 0 15px;
    font-size: 22px;
    border: none;
    transition: all 0.3s;
  }

  & > input:focus {
    outline: none;
  }

  & > i {
    width: 50px;
    height: 50px;
    position: absolute;
    right: 0px;
    top: 0px;
    background-color: orange;
    line-height: 50px;
    font-size: 30px;
    cursor: pointer;
    text-align: center;
    color: white;
    transition: all 0.3s;
  }

  &:hover {
    width: 300px;
    & > input {
      right: 50px;
    }
    & > i {
      border-radius: 50px;
      transform: scale(0.7);
    }
  }
`;
