import React from "react";
import styled from "styled-components";
import BlogContentList from "./ContentList";

export default function BlogContent() {
  return (
    <>
      <ContentContainer>
        <BlogContentList />
      </ContentContainer>
    </>
  );
}

const ContentContainer = styled.div`
  display: flex;
  justify-content: center;
  padding-top: 2rem;
`;
